:root {
  --blue-colour: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 14.06px;
}

body {
  background: #e5e5e5;
  margin: 0;
}

.header-content {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
}

.nav-item:last-child {
  margin-right: 0;
}

.logo_link {
  width: 40px;
  height: 40px;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active:after {
  content: " ";
  background-color: var(--blue-colour);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: var(--blue-colour);
  position: relative;
}

.nav_link:hover {
  color: var(--blue-colour);
}

